 @import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,300;0,900;1,200&family=Roboto:wght@300&family=Source+Code+Pro:ital,wght@1,600&family=Ubuntu&display=swap');


*{
    padding: 0;
    margin: 0;
    font-family: 'Poppins', sans-serif;
}

:root{
    --primary-color:#b450cf;
    --secondary-color:#54335D;
}

body{
    background-color: var(--primary-color);
}

.header{
    background-color: var(--secondary-color);
    height: 100px;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 30px;
}

nav ul{
    display: flex;

}

nav ul li{
    list-style: none;
    margin-right: 1rem;

}

nav ul li a {
    text-decoration: none;
    color:white;
    font-size: 1.2 rem;
    font-weight: bold;
    padding:0.2rem 0.6rem;
    position: relative;
}

nav ul li a::before{
    content: "";
    position: absolute;
    top:-10px;
    left:50%;
    height: 3px;
    width: 0;
    transform: translateX(-50%);
    background-color: var(--primary-color);
    transition: all 0.3s ease-in-out;

}

nav ul li a:hover::before{
    width: 100%;
}

nav ul li a:focus{
    background-color:var(--primary-color);
    opacity: 0.2px;
    border-radius: 20px;
}

nav ul li a:focus::before{
    width: 0%;
}
.Search-btn{
    display: flex;
}

.inputText{
    outline: none;
    border: 1px solid var(--primary-color);
    border-radius: 2rem 0 0 2rem;
    padding: 0.3rem 1rem;
    font-size: 1rem;
    color: white;
    background-color: var(--primary-color);
    box-shadow: inset 0 5px 10px var(--primary-color);
}

.inputText::placeholder{
    color: white;
    
}

.Search-btn button{
    background-color: var(--primary-color);
    box-shadow: inset 0 5px 10px var(--primary-color);
    padding: 0.2rem 0.5rem;
    outline: none;
    border: 1px solid var(--primary-color);
    border-radius:0 2rem 2rem 0;
    color: white;
}

.container{
    margin: auto;
    width:80%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.movie{
    box-shadow: 0 5px 10px rgba(0,0,0,0.5);
    width:250px;
    background-color: var(--secondary-color);
    margin: 1rem;
    border-radius: 5px;
    box-sizing: border-box;
    overflow: hidden;
    position: relative;
}

.movie .poster{
    width: 100%;
    height: 250px;

}

.movie-details{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.movie-details .box{
    width: 100%;
    padding: 1rem;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.movie-details .title{
    width: 160px;
    text-align: center;
}

.movie-details .overview{
    position: absolute;
    left:0;
    right: 0;
    max-height: auto;
    background-color: var(--secondary-color);
    opacity: 0.9;
    color: white;
    box-sizing: border-box;
    padding: 1rem;
    transition: all 0.4s ease-in-out;
    overflow: none;
    transform: translateY(101%);
}

.movie-details:hover .overview{
    transform: translateY(0%);
}

.rating{
    background-color: var(--primary-color);
    width: 45px;
    height: 45px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    border: 1px solid var(--primary-color);
    box-shadow: insert 0 5px 5px rgba(0,0,0,0.5);
    position: absolute;
    right: 5px;
    bottom: 5px;

}

.notfound{
    font-size: larger;
    font-weight: bolder;
    color: red;
}

@media (max-width:900px){
    .header{
        background-color: rgba(0,0,0,0.5);
        height: 160px;}
        nav ul {
            display: flex;
            flex-direction: column;
        }

        nav ul li{
            padding-top: 10px;
        }
        .Search-btn{
            width: 200px;
            margin-right: 100px;
            
        }
}

@media only screen and (max-width:767px) {
    nav ul{
        flex-direction: coloum;
        text-align:centre;
    }

}
